.profile-container{
   display: flex; 
   align-items: center;
   text-align: center;
   justify-content: center;
   height: 10%;
   min-height:fit-content ;
  
}
.profile-parent{
    display: flex;
    align-items: center;
    color: rgb(255, 255, 255);

}
.profile-details-name{
    font-size: 24px;
    font-family: 'Poppins SemiBold';
}
.profile-details-role{
 display: flex;
 flex-direction: column;
 margin: 14px 0 24px 0;
}

.profile-details-role h1{
font-size: 30px;
font-family: 'Times New Roman', Times, serif;
   }

.primary-text{
    color: rgb(255, 255, 255);
}

.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: 'Poppins Light';
}
.profile-options .highlighted-btn{
    margin: 0 0 0 28px;
    color: rgb(255, 255, 255);
    

}
.highlighted-text{
    color: #1f2235;
}
.profile-options .highlighted-btn:hover{
    margin: 0 0 0 28px;
    background-color: aliceblue;
    color: black;

}
.profile-picture{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 1px 0px 0px white;
    border-radius: 50%;
    height:350px;
    width: 300px;
    margin: 0 0px 0 100px;
}
.profile-picture-background{
    height: 92%;
    width: 92%;
    background-image: url('../../../assets/Home/profile2.jpeg');
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}
.profile-picture-background:hover{
    transform: scale(1.07);
    transition: 1s ease-out;

}

.colz .colz-icon i{
    color: white;
    padding: 8px;
    padding-left: 0px;
}
/* Ipad Pro */
@media screen and (max-width: 1110px){
    .profile-picture{
        margin: 0 0px 0 80px;
    }
    .profile-details-name{
        font-size: 20px;
    }
    .profile-details-role .primary-text{
        font-size: 10px;
    }
    .profile-options .btn {
        width: 120px;
        padding: 11px;
        z-index: 700;
    }
}

@media screen and (max-width: 910px){
    .profile-picture{
        height:330px;
        width:290px;
        margin: 0 0 0 4px;
    }

}
@media screen and (max-width: 810px){
    .profile-picture{
        margin: 0!important;
    }
    .profile-parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile-options{
        display: flex;
        justify-content: space-between;
    }
    .profile-details{
        margin: 25px 0 0;

}
    .profile-options .highlighted-btn{
        margin: 0 !important;
    }
    .profile-options, .profile-details-role{
        text-align: center;
    }
}
/* XS */
@media screen and (max-width: 375px){

.profile-picture{
    height:320px;
    width: 275px;
    margin-top: -90px;
}
.profile-details{
    width: 90%;
}
}
