* {
  outline: none;
}

body {
  margin: 0;
  overflow-y: overlay;
  font-family: "Poppins Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 100%, 0.16);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, 0.1);
}

.btn {
  border-radius: 50px;
  padding: 14px 0;
  width: 160px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
}

.btn:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.primary-btn {
  color: var(--white);
  border: 2px solid linen;
  font-size: 12px;
  background-color: #1f2235;
}


.primary-btn:hover {
  color: aliceblue;
  border: 2px solid rgb(49, 126, 190);
  background-color: #1f2235;
}

.highlighted-btn {
  color: var(--white);

  font-size: 12px;
  background-color: #000000;
}
@media only screen and (max-width: 568px) {
  .highlighted-btn {
    z-index: -900;
  }
}

.red-border {
  border: 1px solid var(--error-message) !important;
}

.fade-in {
  opacity: 0;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  transition: all 0.8s ease;
}





.header-container{
    display:flex ;
    justify-content: center;
    height: 140px;
    color: white;

}

.header-parent{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    width: 70%;
    color: white;
}
.header-logo{
    font-size: 30px;
    font-family: 'Futara';
    font-weight: 700;
    color: white;
}

.header-options {
    font-size: 20px!important;
    cursor: pointer;
    font-weight: 600;
    display: flex;
    align-items: center;
}
.header-options div{
    margin: 10px;

}
.header-hamburger{
    display: none;
    color:white;
}
.header-options-seperator {
    margin: 0 50px 0 0;
    color: white;
}
.selected-header-option{
    color:grey;
}

@media only screen and (max-width: 1110px){
    .header-parent{width: 80%}
}
@media only screen and (max-width: 970px){
    .header-hamburger{
        display: block;
    }
    .header-parent{
        width: 100%;
        padding: 0 40px;
        position: relative;
    }
    .header-options{
        display: flex;
        flex-direction: column;
        position: absolute;
        left: -100%;
        top: 0px;
        height: 100vh;
            width: 100%;
            justify-content: space-around;
            background-color: #1f2235;
            z-index:100;
            opacity: .8;
            transition:  all .8s ease-out;
    }
    .header-option-seperator{
        margin: 0;
    }
    .selected-header-option{
        color: white;
    }
    .header-options{
        color:white;
    }
 
    .header-hamburger-bars{
        font-size: 30px;
        cursor: pointer;
        margin: 0px 10px;
        color: white;
    }
    .show-hamburger-options{
        left: 0;
        opacity: 1;
    }

}
